
import { Options, Vue } from "vue-class-component";
import CircleLoader from "@/components/CircleLoader/index.vue";
import api from "@/api";
import * as APIAuth from "@/types/APIAuth";

@Options({
  data() {
    return {
      process: false,
    };
  },

  components: {
    CircleLoader,
  },

  methods: {
    async login() {
      // login process
      const title: HTMLHeadingElement | null =
        document.querySelector(".login h1");
      const btn: HTMLButtonElement | null =
        document.querySelector(".login button");

      if (title && btn) {
        title.style.transform = "translateX(-100vw)";
        btn.style.transform = "translateX(100vw)";
      }

      this.process = true;
      const res: APIAuth.UserAuthURL = await api.backend.auth.getUserAuthURL();

      if (res.state) {
        localStorage.setItem(res.state_key, res.state);
        let popup = window.open(
          res.link,
          "Login with Spotify",
          "width=800,height=600"
        );
        if (popup) {
          window.addEventListener(
            "message",
            (event) => {
              const data = event.data;
              switch (data.status) {
                case "ok":
                  this.$router.push({ name: "Home" });
                  break;
                default:
                  console.log("switch (data.status): default");
              }
            },
            false
          );
        }
      }
    },

    async checkToken(): Promise<boolean> {
      const token: string | null = localStorage.getItem("access_token");
      if (token) {
        const res: APIAuth.CheckToken = await api.backend.auth.checkToken(
          token
        );
        if (res && res.status === "ok") return true;
      }

      return false;
    },
  },

  async mounted() {
    // check token
    const token_valid = await this.checkToken();
    if (token_valid) this.$router.push({ name: "Home" });
  },
})
export default class Home extends Vue {}
